/*
@import "index";
@import "faq";*/

$color:#650300;
$bgcolor:#AA0601;

*,
*::before,
*::after {
    box-sizing: border-box;
}

html,
body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
}

body {
    font-size: 16px;
    line-height: 1.5;
}

img {
    /* RWD */
    max-width: 100%;
    height: auto;
    display: block;
}

a {
    text-decoration: none;
    color: #ffffff;
}
ul {
    padding: 0;
}

.d-flex {
    display: flex;
}

.flex-column {
    flex-direction: column;
}

.justify-content-between {
    justify-content: space-between;

}

.justify-content-center {
    justify-content: center;
}

.align-items-center {
    align-items: center;
}

.mr-8 {
    margin-right: 8px;
}

.mb-8 {
    margin-bottom: 8px;
}

.flex-1 {
    flex-grow: 1;
}

.flex-2 {
    flex-grow: 2;
}

.flex-4 {
    flex-grow: 4;
}

@media (max-width:1920px) {
    .container {
        max-width: 1200px;
        margin: 0 auto;
    }
}

@media (max-width:768px) {
    .container {
        max-width: 672px;
    }
}

@media (max-width:375px) {
    .container {
        max-width: 350px;
    }

    .footerList {
        display: none;
    }
}

/*
header */
header {
    background-color: $bgcolor;
    color: #ffffff;
}

/*
nav */
.navBar {   /* 網站會有多個 nav 標籤,勿寫在html標籤上 */
    height: 108px;
    padding-left: 88px;
    padding-right: 88px;

    h1 {
        width: 88px;
        height: 49px;
        background: url(../images/logo.png) no-repeat;
    }

    ul {
        display: flex;
        justify-content: flex-end;

        li {
            font-size: 20px;
            margin-left: 64px;
            a:hover {
                font-weight: 700;
            }
        }
    }
}

/*
footer  */
footer {
    color: #ffffff;
    background-color: $bgcolor;
    padding-top: 48px;
}

.footerLinks {
    align-items: center;
    margin-bottom: 32px;

    ul {
        justify-content: start;

        li {
            margin-right: 48px;
        }
    }
}

.footerContent li {
    display: flex;
    align-items: center;
    font-size: 2rem;
    margin-bottom: 22px;
}

.footerContent img {
    width: 48px;
    margin-right: 12px;
}

.footerSns img {
    width: 48px;
    margin-left: 32px;
}

.footerLast {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #fff;
    padding-top: 24px;
    margin-bottom: 27px;

    h5 span {
        margin-left: 62px;
    }
}

.footerLast .copyright {
    display: flex;
    align-items: center;

    img {
        width: 80px;
        margin-right: 38px;
    }
}
@media (max-width:768px) {
    .navBar {
        padding-left: 48px;
        padding-right: 48px;
        h1 {
            width: 80px;

        }
            ul li {
            margin-left: 28px;
        }
    }
    .copyright {
        img {
            display: none;
        }
        h5 {
            font-style: 14px;
        }
    }
    .footerLinks ul li {
        margin-right: 40px;
    }
    .footerContent img {
        width: 23px;
        margin-right: 12px;
    }
    .footerSns img {
        width: 24px;
        margin-left: 24px;
    }
}


/* AOS */
[data-aos="text-translateX-left"] {
    transform: translateX(0px);
    &.aos-animation {
        transform: translateX(-50px);
    }
}
/*視差-圖靠下*/
[data-aos="bgImg-position-down"] {
    &.aos-animate {
        background-position: bottom center;
    }
}

/*  
topImg  常搞不懂背景圖與標題的愛恨糾葛... */
.topImg {
    height: 840px;
    background: url(../images/banner.png) no-repeat;
    background-position: center center;
    background-size: cover;

    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 80px;

    .title {
        color: $color;
        text-align: right;
    }

    h2 {
        font-size: 64px;
        font-weight: 600;
        line-height: 96px;
    }

    h3 {
        font-size: 36px;
        line-height: 54px;
    }
}
