/*
info    */
.info {
    background-color: #AA06010D;
    padding-top: 64px;
    margin-bottom: 64px;

    h3 {
        color: $color;
        font-size: 3rem;
        font-weight: 600;
        text-align: center;
        margin-bottom: 60px;
    }

    .item {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-wrap: wrap;

        li {
            width: 210px;
            margin-bottom: 80px;
            display: flex;
            flex-direction: column;
            align-items: center;
            img {
                width: 100px;
                height: 100px;
                /*margin: 0 25% 40px;*/
                margin-bottom: 2.5rem;
            }
            
    
            h4 {
                color: $color;
                font-size: 1.5rem;
                font-weight: 600;
                text-align: center;
                line-height: 36px;
            }
        }

        
    }
}

/*
product 經典系列鏡框*/
.product {
    margin-bottom: 80px;

    h2 {
        color: $color;
        font-size: 3rem;
        font-weight: 600;
        margin-bottom: 60px;
        text-align: center;
    }

    .productItem li {
        display: flex;
        margin-bottom: 2rem;

        &:last-child {
            margin-bottom: 0;
        }

        h3 {
            display: flex;
            align-items: center;
            color: $bgcolor;
            font-size: 3rem;
            font-weight: 700;
            font-style: italic;
            text-transform: uppercase;
            margin-left: 1.5rem;

            img {
                margin-right: 8px;
            }
        }

        &:nth-child(2) {
            h3 {
                margin-right: 1.5rem;
            }

            img {
                width: 100%;
            }
        }

        .colorItem {
            position: relative;

            &::before {
                content: '';
                position: absolute;
                background: url(../images/product-2-color.png) no-repeat;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                opacity: 0;
                background-size: cover;
                /*contain 使背景圖在尺寸大於內容元素的情況下，得以完整呈現。*/
                background-position: center center;
            }

            &:hover::before {
                opacity: 1;
            }
        }
    }
}


/*
co-Brand 聯名設計鏡框 */

.coBrand {
    background: url(../images/bg.png) no-repeat;
    background-size: cover;
    background-position: top center;
    padding-top: 64px;
    /*padding-bottom: 64px;*/
    padding-bottom: 400px;

    h2 {
        color: #ffffff;
        font-size: 3rem;
        font-weight: 600;
        text-align: center;
        margin-bottom: 60px;
    }
}

.coBrandList {
    display: flex;

    li:first-child {
        margin-bottom: 3.75rem;
    }
}


.itemBrand {
    position: relative;

    &::after {
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba($color: #ffffff, $alpha: .5);
    }

    span {
        color: $bgcolor;
        font-size: 72px;
        font-weight: 600;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        /* transform 垂直置中 */
        z-index: 1;
        white-space: nowrap;
        /* 不換行 */
    }

    &:first-of-type {
        margin-right: 1.25rem;
    }
}
/*  more魔王 */
.moreItem { 
    align-items: flex-end;
    span {
        position: absolute;
        color: $bgcolor;
        font-size: 4.5rem;
        font-weight: 700;
        transform: translate(0%,20%);
        white-space: nowrap;
        z-index: 1;

        &::before {
            content: 'MORE';
            position: absolute;
            transition: all .6s .3s;
            transform: translateY(0);
        }
        &::after {
            content: 'MORE';
            position: absolute;
            transition: all .6s .3s;
            transform: translateY(0);
        }
        &:hover::before {
            transform: translateY(-85px);
            opacity: .7;
        }
        &:hover::after {
            transform: translateY(-145px);
            opacity: .3;
        }
    }
}
.moreImg {  
    position: relative;
    height: 100%;
    max-width: 30%;
    transition: all .6s .3s;
    transform: translateX(0);
    margin-right: 85px;

    &::after {
        content: '';
        position: absolute;
        background: #ffffff;
        top: 15%;
        bottom: 15%;
        left:10%;
        right: -85px;
    }
    &:hover img:last-child {
        transform: translateX(15px);
        transition: all .8s; /* 待修正, arrow動畫怪怪的 */
    }
    span {
        padding: 5px 0 5px 32px;
    }
}


/*
commend 顧客推薦 */
.commend {
    margin-top: -300px;
    margin-bottom: 80px;

    h2 {
        color: #ffffff;
        font-size: 3rem;
        font-weight: 600;
        text-align: center;
        margin-bottom: 60px;
    }

    li {
        width: 282px;        
        background-color: #fff;

        .commendTxt {
            padding: 16px;
            border: 1px solid #DEE2E6;

            h4 {
                font-weight: 600;
                margin-bottom: 16px;
            }            

            p {
                margin-bottom: 16px;
            }
            .time {
                color: #707070;
            }
        }
    }
}

/*
contactUs  聯絡我們 */
.contactUs {
    background-color: #AA06010D;
    padding-top: 64px;
    padding-bottom: 92px;

    .container {
        width: 800px;

        h3 {
            color: #000;
            font-size: 3rem;
            font-weight: 600;
            text-align: center;
            margin-bottom: 24px;
        }

        p {
            font-size: 20px;
            line-height: 30px;
            margin-bottom: 15px;
        }
    }
}

.form-group {
    /*display: flex;
    flex-direction: column;*/

    label {
        margin-bottom: 5px;
    }

    input,textarea {
        border: 1px solid #000;
        padding: 14px 16px;
        margin-bottom: 16px;
        background: transparent;    /*透明色*/
        outline: 0; /* 會影響到無障礙tab作用嗎? */
    }
    textarea {
        height: 8.5rem;
        resize: none;
    }
    input::placeholder {
        color: rgba(0,0,0,.2);
    }

    textarea::placeholder {
        color: rgba(0,0,0,.2);
    }

    .agree {
        margin-bottom: 16px;
    }
}

.btn {
    display: block;
    margin: 0 auto;
    width: 180px;
    font-family: "Roboto", "Microsoft JhengHei";
    border: none;
    font-size: 16px;
    color: #ffffff;
    background-color: $bgcolor;
    padding: 16px 58px;

    &:hover {
        cursor: pointer;
        background-color: $color;
    }
}

/*
Faq 常見問題 */
