/* AOS */
[data-aos="text-translateX-left"] {
  transform: translateX(0px); }
  [data-aos="text-translateX-left"].aos-animation {
    transform: translateX(-50px); }

/*視差-圖靠下*/
[data-aos="bgImg-position-down"].aos-animate {
  background-position: bottom center; }

/*  
