/*
info    */
.info {
  background-color: #AA06010D;
  padding-top: 64px;
  margin-bottom: 64px; }
  .info h3 {
    color: #650300;
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 60px; }
  .info .item {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap; }
    .info .item li {
      width: 210px;
      margin-bottom: 80px;
      display: flex;
      flex-direction: column;
      align-items: center; }
      .info .item li img {
        width: 100px;
        height: 100px;
        /*margin: 0 25% 40px;*/
        margin-bottom: 2.5rem; }
      .info .item li h4 {
        color: #650300;
        font-size: 1.5rem;
        font-weight: 600;
        text-align: center;
        line-height: 36px; }

/*
product 經典系列鏡框*/
.product {
  margin-bottom: 80px; }
  .product h2 {
    color: #650300;
    font-size: 3rem;
    font-weight: 600;
    margin-bottom: 60px;
    text-align: center; }
  .product .productItem li {
    display: flex;
    margin-bottom: 2rem; }
    .product .productItem li:last-child {
      margin-bottom: 0; }
    .product .productItem li h3 {
      display: flex;
      align-items: center;
      color: #AA0601;
      font-size: 3rem;
      font-weight: 700;
      font-style: italic;
      text-transform: uppercase;
      margin-left: 1.5rem; }
      .product .productItem li h3 img {
        margin-right: 8px; }
    .product .productItem li:nth-child(2) h3 {
      margin-right: 1.5rem; }
    .product .productItem li:nth-child(2) img {
      width: 100%; }
    .product .productItem li .colorItem {
      position: relative; }
      .product .productItem li .colorItem::before {
        content: '';
        position: absolute;
        background: url(../images/product-2-color.png) no-repeat;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        opacity: 0;
        background-size: cover;
        /*contain 使背景圖在尺寸大於內容元素的情況下，得以完整呈現。*/
        background-position: center center; }
      .product .productItem li .colorItem:hover::before {
        opacity: 1; }

/*
co-Brand 聯名設計鏡框 */
.coBrand {
  background: url(../images/bg.png) no-repeat;
  background-size: cover;
  background-position: top center;
  padding-top: 64px;
  /*padding-bottom: 64px;*/
  padding-bottom: 400px; }
  .coBrand h2 {
    color: #ffffff;
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 60px; }

.coBrandList {
  display: flex; }
  .coBrandList li:first-child {
    margin-bottom: 3.75rem; }

.itemBrand {
  position: relative; }
  .itemBrand::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(255, 255, 255, 0.5); }
  .itemBrand span {
    color: #AA0601;
    font-size: 72px;
    font-weight: 600;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    /* transform 垂直置中 */
    z-index: 1;
    white-space: nowrap;
    /* 不換行 */ }
  .itemBrand:first-of-type {
    margin-right: 1.25rem; }

/*  more魔王 */
.moreItem {
  align-items: flex-end; }
  .moreItem span {
    position: absolute;
    color: #AA0601;
    font-size: 4.5rem;
    font-weight: 700;
    transform: translate(0%, 20%);
    white-space: nowrap;
    z-index: 1; }
    .moreItem span::before {
      content: 'MORE';
      position: absolute;
      transition: all .6s .3s;
      transform: translateY(0); }
    .moreItem span::after {
      content: 'MORE';
      position: absolute;
      transition: all .6s .3s;
      transform: translateY(0); }
    .moreItem span:hover::before {
      transform: translateY(-85px);
      opacity: .7; }
    .moreItem span:hover::after {
      transform: translateY(-145px);
      opacity: .3; }

.moreImg {
  position: relative;
  height: 100%;
  max-width: 30%;
  transition: all .6s .3s;
  transform: translateX(0);
  margin-right: 85px; }
  .moreImg::after {
    content: '';
    position: absolute;
    background: #ffffff;
    top: 15%;
    bottom: 15%;
    left: 10%;
    right: -85px; }
  .moreImg:hover img:last-child {
    transform: translateX(15px);
    transition: all .8s;
    /* 待修正, arrow動畫怪怪的 */ }
  .moreImg span {
    padding: 5px 0 5px 32px; }

